<template>
	<div id="memberProfilePassword">
		<public-header title="修改密码"></public-header>
		<div class="content">
			<van-cell-group class="margin-10-t">
				<van-field v-model="password" type="password" placeholder="当前密码" />
				<van-field v-model="newpassword" type="password" placeholder="新密码" />
				<van-field v-model="repassword" type="password" placeholder="确认新密码" />
			</van-cell-group>
			<div class="padding-15">
				<van-button size="normal" type="primary" @click="onSubmit()" :disabled="!islegal" block class="font-16">确定</van-button>
			</div>
			<div class="c-gray text-center font-14">忘记旧密码? 请联系平台管理员</div>
		</div>
	</div>
</template>

<script>
	import PublicHeader from '@/components/header'
	import {setData} from "@/controller/funcCommon.js"
	export default {
		data() {
			return {
				password: '',
				newpassword: '',
				repassword: '',
				islegal: true
			}
		},
		components: {
			PublicHeader,
		},
		methods: {
			onSubmit() {
				var that = this;
				if(!that.islegal) {
					return false;
				}
				if (!that.password) {
					that.$toast('当前密码不能为空');
					return false;
				}
				if (!that.newpassword) {
					that.util.$toast('新密码不能为空');
					return false;
				}
				if (!that.repassword) {
					that.util.$toast('请确认新密码');
					return false;
				}
				if(that.newpassword != that.repassword) {
					that.util.$toast('两次密码输入不一致');
					return false;
				}
				that.islegal = false;
				var data = {
					password: this.password,
					newpassword: this.newpassword,
					repassword: this.repassword
				}
				setData({
					vue: that,
					url: 'plateform/member/mine/password',
					data: data,
					message: '修改成功',
					redirect: that.util.getUrl({path: '/pages/member/mine'}),
					fail: (res) => {
						that.util.$toast(res);
						that.islegal = true;
					}
				})
			},
		},
	}
</script>

<style>

</style>



